class Modal{
	constructor(){
		this._modal = document.getElementById('modal');
		this._modalContent = document.getElementById('modal-content');
		this.open = false;
		this._Init();
	}

	_Init(){
		console.log(this._modal);
		document.getElementById('modal-close-btn').addEventListener('click', () => {
			this._Close();
		});
	}

	Open(content){
		this._modal.style.display = "flex";
		this.open = true;
		this._LoadContent(content)
	}

	_Close(){
		this._modal.style.display = "none";
		this.open = false;
	}

	_LoadContent(content){
		let curContent;
		switch(content){
			case 'corona-counter':
				curContent = `
				<h1>corona-counter</h1>
				<a href="https://edwardleks.github.io/corona_counter/">see here</a>
				`;
				break;
			case 'km-panel':
				curContent = `
				<h1>km-panel</h1>
				<a href="http://km-panel.ee/">see here</a>
				`;
				break;
			case 'simargl':
				curContent = `
				<h1>simargl</h1>
				<p>Unfortunately their webpage is down (I will upload work soon)<p>
				`;
				break;
			case 'salong24':
				curContent = `
				<h1>salong24</h1>
				<a href="https://salong24.ee/">see here</a>
				`;
				break;
			case 'about':
				curContent = `
				<h1>about</h1>
				<p>There will be soon information about me 🙂</p>`;
				break;
			default:
				curContent = `<h1>Something<h1>`;
				break;
		}
			
		this._modalContent.innerHTML = curContent;
	}
}

export default Modal;